<template>
  <div>
    <panel title=" รายละเอียดสินค้า">
      <template v-slot:header-right> </template>
      &nbsp;
      <div class="vx-row">
        <div class="vx-col w-1/12 bold">ชื่อ (TH)</div>
        :
        <div class="vx-col w-1/12">
          {{ form.setting_master_product.name_th }}
        </div>
        <div class="vx-col w-1/12"></div>

        <div class="vx-col w-1/12 bold">ชื่อ (EN)</div>
        :
        <div class="vx-col w-1/12">
          {{ form.setting_master_product.name_en }}
        </div>
        <div class="vx-col w-1/12"></div>

        <div class="vx-col w-1/12 bold">รหัส</div>
        :
        <div class="vx-col w-1/12">{{ form.code }}</div>
        <div class="vx-col w-1/12"></div>

        <div class="vx-col w-1/12 bold">รายละเอียด</div>
        :
        <div class="vx-col w-1/12">{{ form.description }}</div>
      </div>
      &nbsp;
      <div class="vx-row" v-if="form.setting_master_product.type == 'อะไหล่'">
        <div class="vx-col w-1/12 bold">จำนวน</div>
        :
        <div class="vx-col w-1/12">{{ form.quantity }}</div>
        <div class="vx-col w-1/12"></div>

        <div class="vx-col w-1/12 bold">ราคาขายส่ง</div>
        :
        <div class="vx-col w-1/12">{{ form.wholesale_price }}</div>
        <div class="vx-col w-1/12"></div>

        <div class="vx-col w-1/12 bold">ราคาขายปลีก</div>
        :
        <div class="vx-col w-1/12">{{ form.retail_price }}</div>
        <div class="vx-col w-1/12"></div>
        <div class="vx-col w-1/12 bold">แท็ก</div>
        :
        <div class="vx-col w-1/2">
          <vs-chip v-for="tag in form.tags" :key="tag.text">{{
            tag.text
          }}</vs-chip>
        </div>
      </div>

      <div class="vx-row" v-if="form.setting_master_product.type !== 'อะไหล่'">
        <div class="vx-col w-1/12 bold">ราคาขายส่ง</div>
        :
        <div class="vx-col w-1/12">{{ form.wholesale_price }}</div>
        <div class="vx-col w-1/12"></div>

        <div class="vx-col w-1/12 bold">ราคาขายปลีก</div>
        :
        <div class="vx-col w-1/12">{{ form.retail_price }}</div>
        <div class="vx-col w-1/12"></div>
        <div class="vx-col w-1/12 bold">แท็ก</div>
        :
        <div class="vx-col w-1/2">
          <vs-chip v-for="tag in form.tags" :key="tag.text">{{
            tag.text
          }}</vs-chip>
        </div>
      </div>

      &nbsp; &nbsp;

      <vs-row>
        <vs-col vs-align="center">
          <vs-button color="primary" @click="editData(form)" type="filled"
            >แก้ไข</vs-button
          ></vs-col
        >
      </vs-row>
    </panel>
    &nbsp;
    <panel title=" รูปภาพ">
      <attached
        :attached_id="form.id"
        model="App\\Models\\Product\\Product"
        :readOnly="true"
        ref="upload"
      ></attached>
      <vs-row>
        <vs-col vs-type="flex" vs-justify="flex-end" vs-align="center">
          <vs-button color="primary" type="filled" to="/product/checkWarehouse"
            >กลับ</vs-button
          ></vs-col
        >
      </vs-row>


    </panel>
  </div>
</template>

<script>
import vSelect from 'vue-select'
export default {
  components: {
    vSelect
  },
  data () {
    return {
      form: {
        id: '',
        code: '',
        vin: '',
        tags: [],
        name_en: '',
        description: '',
        quantity: '',
        retail_price: '',
        wholesale_price: '',
        setting_master_product_id: ''
      }
    }
  },
  methods: {
    setData () {
      this.form.id = this.$route.query.id
    },
    async loadData () {
      try {
        const { data } = await this.$http.get(`/api/product/${this.form.id}`)

        this.form = data.data
      } catch (error) {
        throw error
      }
    },

    editData (form) {
      this.$router.push({ name: 'product_edit', query: { id: form.id } })
    }
  },
  mounted () {
    this.setData()
    this.loadData()
  }
}
</script>


<style scoped>
ิ.bold {
  font-weight: bold;
}
</style>
